.temp{
    position: absolute;
    width: 375px;
    height: 812px;
    background: "#FFFFFF";
    border-radius: 44px;
    left: 10px;
}

/* LOADING */
.temp_loadingImg{
    position: absolute;
    width: 62px;
    height: 75px;
    left: 157px;
    top: 368px;
}

/* STATUS */
.temp_status{
    position: absolute;
    height: 44px;
    left: 0px;
    right: 0px;
    top: 0px;
}
.temp_status_left{
    position: absolute;
    width: 54px;
    height: 20px;
    left: 19px;
    top: 17px;

    font-family: SF Pro Text;
    font-style: normal;
    font-weight: 600;
    font-size: 15px;
    line-height: 20px;
    /* identical to box height, or 133% */

    text-align: center;
    letter-spacing: -0.5px;

    /* Label Color/Light/Primary */

    color: #000000;
}
.temp_status_right{
    position: absolute;
    width: 66.66px;
    height: 11.34px;
    left: 297.67px;
    top: 21.33px;
}

/* NOTCH ERROR */
.temp_notch{
    position: absolute;
    width: 164px;
    height: 31px;
    left: 106px;
    top: -2px;
    background: #e5e5e5;
    /* margin: 20px; */
}
.temp_status_notch{
    position: absolute;
    width: 31px;
    height: 31px;
    left: calc(50% - 164px/2 + 0.5px);
    /* left: 106px; */
    top: -2px;
    background: #e5e5e5;
    /* margin-right: 20px; */
}

/* NAV UPPER */
