    font-family: Roboto;
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 85.19%; /* or 10px */
    display: flex;
    align-items: center;
    color: #5F5F5F;
}
.temp_nav_search{
    position: absolute;
    width: 24px;
    height: 24px;
