.main{
    position: absolute;
    width: 375px;
    height: 812px;
    background: "#FFFFFF";
    border-radius: 44px;
    left: 10px;
    
    border-style: solid;
    border-width: 5px;
    border-color: black;
}
.main_content{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0px;
    position: absolute;
    width: 375px;
    height: 629px;
    left: 0px;
    top: 102px;
    overflow-y: scroll;
    background: #F2F2F2;

    -ms-overflow-style: none;
    scrollbar-width: none;
    /* background: #E8E8ED; */
}
.main_content::-webkit-scrollbar{
    display: none;
}




